import List04 from './list-components/list-04.vue'

let leftHeight = 0
let rightHeight = 0

export default {
	components: { List04 },
	props: {
		xiaoJiInfo: {
			type: Array
		}
	},
	data() {
		return {
			leftList: [],
			rightList: []
		}
	},
	mounted () {



	},
	methods: {
		makeMasonry () {
			this.xiaoJiInfo.forEach((item, index) => {
				this.getImageRate(item)
			})
		},
		getImageRate (item) {
			let self = this
			uni.getImageInfo({
				src: item.img,
				success: (image) => {
					let rate = image.height / image.width
					if (leftHeight <= rightHeight) {
						this.leftList.push(item)
						leftHeight += rate
					} else {
						this.rightList.push(item)
						rightHeight += rate
					}
				},
				fail: function (e) {
					console.log(e)
				}
			})
		},
		// _getPicRate: async function (url) {
		// 	let pic = await this._createPic(url)
		// 	let rate = pic.height / pic.width
		// 	return rate
		// },
